<template>
    <div class="from-view-block from-view-block--add-file">
        <div style="height: 0!important; width: 0; padding: 0 !important; overflow: hidden!important;">
            <input
                type="file"
                :disabled="disabled === true || processing === true"
                :multiple="multi"
                @change="changeHandler" />
        </div>
        <div v-if="files.length > 0" class="file-list" ref="mainContainer">
            <div v-for="(file, key) in files" class="file-list__item">
                <div class="file-card">
                    <slot name="file" v-bind="{file: file, key: key}">
                        <div class="file-card__img">
                            <img class="img-pre"
                                 v-if="!files_preview[key] && file.type.match('application/pdf')"
                                 src="data:image/svg+xml;base64,">
                            <img class="img-pre"
                                 v-else-if="!files_preview[key] && (!file.type || file.type.match('image.*'))"
                                 :src="files_preview[key]">
                            <img class="img-pre"
                                 v-else="files_preview[key]"
                                 src="data:image/svg+xml;base64,">
                        </div>
                        <div class="file-card__info">
                            <span class="name" v-if="fileName === true">{{ file.name }}</span>
                            <span class="size">size: {{ Math.ceil(file.size / 1024) }}Кб</span>
                        </div>
                    </slot>
                    <a class="remove" v-show="file" @click="removeFile(key, file)"></a>
                </div>
            </div>
        </div>
        <div class="file-buttons">
            <a @click="chooseFiles()">{{buttonText ? buttonText : (multi ? 'Добавить' : 'Выбрать')}}</a>
        </div>
    </div>
</template>

<script>
export default {
  name: 'FormFile',
  props: {
    value: {
      required: false
    },
    /**
    * Button text
    * @type {String}
    */
    buttonText: {
      type: String,
      required: false,
      default: ''
    },
    /**
     * @type {String}
     */
    fileName: {
      type: Boolean,
      default: function () {
        return false
      }
    },
    multiple: {
      type: Boolean,
      default: function () {
        return false
      }
    }
  },
  data: function () {
    return {
      files: [],
      files_preview: []
    }
  },
  mounted: function () {
    if (this.$refs.mainContainer) {
      this.$refs.mainContainer.addEventListener('drop', this.onDrop, true)
    } else {
      // console.warn('ref mainContainer not found')
    }
  },
  methods: {
    changeHandler: function (e) {
      let files = e.target.files || e.dataTransfer.files
      console.log('changeHandler:', files)
      if (!files.length) {
        return
      }
      // get files preview
      let __this = this
      for (let file of files) {
        if (file instanceof File && file.type.match('image.*')) {
          console.log(file.type, 'create preview')
          var reader = new FileReader()
          reader.onload = ((theFile) => {
            return function (e) {
              if (__this.multiple === true) {
                __this.files.push(theFile)
                __this.files_preview.push(e.target.result)
              } else {
                __this.files = [theFile]
                __this.files_preview = [e.target.result]
              }
            }
          })(file)
          reader.readAsDataURL(file)
        } else {
          console.log(file.type, 'add without preview')
          if (__this.multiple === true) {
            __this.files.push(file)
            __this.files_preview.push('')
          } else {
            __this.files = [file]
            __this.files_preview = ['']
          }
        }
      }
      // console.log('file ... ok', this.files)
    },
    eventOnElement (event, object) {
      let rect = object.getBoundingClientRect()
      return event.pageX >= rect.left && event.pageY >= rect.top && event.pageX <= rect.right && event.pageY <= rect.bottom
    },
    onDrop (e) {
      e.stopPropagation()
      e.preventDefault()
      if (!e.dataTransfer.files || !this.eventOnElement(e, this.$refs.mainContainer)) {
        return
      }
      if (!e.dataTransfer.files || !e.dataTransfer.files[0]) {
        return
      }
      if (!/^image\//.test(e.dataTransfer.files[0].type)) {
        return
      }
      this.selectImage(e.dataTransfer.files[0])
    },
    chooseFiles: function () {
      this.$el.querySelector('input').click()
    },
    removeFile (key) {
      this.files.splice(key, 1)
      this.files_preview.splice(key, 1)
    }
  },
  computed: {
    multi () {
      return this.multiple === true
    }
  },
  watch: {
    value: function (val) {
      if (typeof val === 'undefined') {
        this.files = []
        this.files_preview = []
      }
    },
    files: function (files) {
      console.log('files change, multiple=', this.multiple, files)
      if (this.multiple === true) {
        let dt = new DataTransfer()
        Object.keys(files).map(($kye) => {
          dt.items.add(files[$kye])
        })
        console.log('file emit more')
        this.$emit('input', dt.files)
      } else {
        Object.keys(files).map(($key) => {
          console.log('file emit one', files[$key])
          this.$emit('input', files[$key])
        })
      }
    }
  }
}
</script>



